@import "https://fonts.googleapis.com/css2?family=Montserrat:wght@600&display=swap";
body {
  font-family: Montserrat, sans-serif;
}

.app {
  justify-content: center;
  padding: 5rem 0 0;
  display: flex;
}

.wrap {
  width: 500px;
}

.select {
  width: 100%;
  z-index: 100;
  position: relative;
}

.select__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.select__item {
  border-bottom: 1px solid #ccc;
  padding: .8rem 20px;
}

.select__item:hover {
  cursor: pointer;
  background: #cccc;
  transition: background .3s ease-in;
}

.select__item.selected {
  background: #ccc;
}

.select.open .select__dropdown, .select.open .select__backdrop {
  display: block;
}

.select__input {
  height: 46px;
  border: 1px solid #ccc;
  border-radius: 5px 5px 0 0;
  justify-content: space-between;
  align-items: center;
  padding: 0 1rem;
  transition: all .3s ease-in;
  display: flex;
}

.select__input:hover {
  cursor: pointer;
  background: #cccc;
}

.select__input:last-child {
  transform: rotate(-90deg);
}

.select__dropdown {
  max-height: 200px;
  border: 1px solid #ccc;
  border-radius: 0 0 5px 5px;
  display: none;
  position: absolute;
  top: 46px;
  left: 0;
  right: 0;
  overflow-y: auto;
  box-shadow: 2px 3px 5px #0000004d;
}

.select__backdrop {
  z-index: -1;
  display: none;
  position: fixed;
  inset: 0;
}

